.Navbar {
  height: 50px;
  border-bottom: 0.5px solid rgb(231, 228, 228);
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #555;

  .wrapper {
    width: 100%;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .search {
    display: flex;
    align-items: center;
    border: 0.5px solid lightgrey;
    padding: 3px;
  }
  input {
    border: none;
    outline: none;
    background: transparent;
    &::placeholder {
      font-size: 12px;
    }
  }

  .items {
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  .item {
    display: flex;
    align-items: center;
    margin-right: 20px;
    position: relative;
    .icon {
      font-size: 25px;
    }
  }
  .avatar {
    width: 30px;
    height: 30px;
    border-radius: 50%;
  }
  .counter {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: red;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    color: white;
    font-weight: bold;
    position: absolute;
    top: -1px;
    right: -1px;
  }
}
// MEDIA QUERIES FOR TABLET
@media screen and (max-width: 1024px) {
  .Navbar .search {
    display: none;
  }
  .Navbar .items {
    margin-left: 50px;
  }
}
